.header {
  position: absolute;
  display: flex;
  justify-content: center;
  height: 90px;
  width: 100%;
  top: 0;
  z-index: 3;
}
.header .container .header__logo {
  display: flex;
  align-items: center;
  width: 25%;
}
.header .container .header__logo a {
  display: flex;
  align-items: center;
  color: white;
}
.header .container .header__logo a h1 {
  letter-spacing: 2px;
  font-weight: 400;
}
.header .container .header__logo a h1:nth-of-type(1) {
  font-size: 36px;
}
.header .container .header__logo a h1:nth-of-type(2) {
  font-size: 24px;
}
.header .container .header__logo a .logo__divisor {
  background-color: white;
  height: 30px;
  width: 2px;
  margin: 0 12px;
}
.header .container .header__menu {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 65%;
}
.header .container .header__menu ul {
  margin-top: 5px;
}
.header .container .header__menu ul li {
  margin: 0 3px;
}
.header .container .header__menu ul li a {
  padding: 5px 6px;
  color: white;
  font-size: 16px;
  letter-spacing: 1px;
  text-transform: uppercase;
  border-bottom: 1px solid transparent;
}
.header .container .header__menu ul li a:hover {
  border-bottom: 1px solid white;
  cursor: pointer;
}
.header .container .header__menu ul li a.header__menu--selected {
  border-bottom: 1px solid white;
}
.header .container .header__icons {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  width: 10%;
}
.header .container .header__icons .header__icon {
  width: fit-content;
  padding: 0 8px;
}
.header .container .header__icons .header__icon .icon {
  font-size: 20px;
}
.header .container .header__icons .header__icon .icon:hover {
  cursor: pointer;
}
.header .container .header__menu_responsive {
  display: none;
  align-items: center;
  justify-content: flex-end;
  width: 8%;
}
.header .container .header__menu_responsive .icon {
  font-size: 20px;
}
.header .container .header__menu_responsive .icon:hover {
  cursor: pointer;
}
.header .container .header__menu_responsive .header__menu_responsive__container {
  position: absolute;
  background-color: white;
  border: 2px solid #e9e9e9;
  box-shadow: 0 0 10px rgba(77, 77, 77, 0.4);
  top: 0;
  margin-top: 70px;
  border-radius: 5px;
}
.header .container .header__menu_responsive .header__menu_responsive__container ul {
  flex-direction: column;
  align-items: center;
  padding: 5px 0;
}
.header .container .header__menu_responsive .header__menu_responsive__container ul li {
  border-radius: 5px;
  width: 100%;
  padding: 6px 50px;
  text-align: center;
}
.header .container .header__menu_responsive .header__menu_responsive__container ul li a {
  color: black;
  font-size: 18px;
  letter-spacing: 1px;
}
.header .container .header__menu_responsive .header__menu_responsive__container ul li:hover {
  background-color: rgba(228, 228, 228, 0.5);
  cursor: pointer;
}

.main-banner {
  background-image: url("/assets/images/main_banner.jpg");
  background-position: center;
  background-size: cover;
  position: relative;
  width: 100%;
  height: 100vh;
}
.main-banner a .main-banner__gradient {
  background-image: linear-gradient(180deg, rgba(0, 0, 0, 0.6), transparent);
  position: absolute;
  width: 100%;
  height: 100%;
}

.about-us {
  padding-top: 42px;
}
.about-us .about-us__left {
  position: relative;
}
.about-us .about-us__left .about-us__left__background {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  z-index: -1;
  background-image: url("/assets/images/vult_banner_04.jpg");
  background-position: center;
  background-size: cover;
  filter: blur(2px);
  opacity: 0.1;
}
.about-us .about-us__right {
  position: relative;
}
.about-us .about-us__right .about-us__images img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.about-us .about-us__right .about-us__images.first {
  position: absolute;
  width: 50%;
  height: 440px;
  right: 0;
  top: 35%;
  z-index: 100;
}
.about-us .about-us__right .about-us__images.second {
  position: absolute;
  width: 60%;
  height: 440px;
  right: 40px;
}
.about-us .about-us__right .about-us__images.third {
  position: absolute;
  width: 440px;
  height: 440px;
  right: 100%;
  top: 100%;
  transform: translate(50%, -50%);
  z-index: 99;
}

.store .store__right {
  position: relative;
}
.store .store__right img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.text-banner {
  display: flex;
  justify-content: center;
  margin-bottom: 64px;
}
.text-banner .text-banner__text {
  font-size: 34px;
  width: calc(100% - 400px);
  margin: 150px 200px 16px 200px;
  text-align: center;
  line-height: 1.5;
}

.feature {
  margin-bottom: 0;
}
.feature .feature__container {
  align-items: flex-end;
}
.feature .feature__left {
  background-image: linear-gradient(180deg, transparent, rgba(153, 153, 153, 0.1));
  height: 800px;
}
.feature .feature__right {
  position: relative;
  height: inherit;
}
.feature .feature__right img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  transition: opacity 0.8s ease-in;
}
.feature .feature__right img.model_1 {
  opacity: 1;
}
.feature .feature__right img.model_2 {
  opacity: 0;
}
.feature .feature__right img.model_3 {
  opacity: 0;
}
.feature .feature__right img.model_4 {
  opacity: 0;
}
.feature .feature__right img.model_5 {
  opacity: 0;
}
.feature .feature__right img.model_6 {
  opacity: 0;
}

.news {
  display: flex;
  justify-content: center;
  height: 1400px;
  margin-bottom: 80px;
}
.news .container {
  background-image: linear-gradient(180deg, rgba(153, 153, 153, 0.1), transparent);
  padding: 80px;
  padding-top: 180px;
  justify-content: center;
  align-items: center;
  position: relative;
}
.news .container .news__container {
  position: absolute;
  display: flex;
  flex-direction: column;
  width: 440px;
  height: fit-content;
}
.news .container .news__container img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 0.5;
  transition: 0.2s;
}
.news .container .news__container img:hover {
  opacity: 1;
}
.news .container .news__container .news__container__small__image {
  background-color: cornsilk;
  width: 440px;
  height: 440px;
}
.news .container .news__container .news__container__large__image {
  background-color: darkgray;
  width: 440px;
  height: 560px;
}
.news .container .news__container .news__container__content {
  padding: 20px 60px;
  margin-top: 15px;
  font-size: 18px;
}
.news .container .news__container .news__container__content .news__container__links {
  display: flex;
  flex-direction: column;
  text-transform: uppercase;
}
.news .container .news__container .news__container__content .news__container__links a {
  color: var(--dark-grey);
  text-decoration: underline;
}
.news .container .news__container .news__container__content .news__container__title {
  font-size: 26px;
  margin: 14px 0;
}
.news .container .news__container:nth-of-type(1) {
  left: 5%;
  z-index: 2;
  margin-bottom: 90px;
}
.news .container .news__container:nth-of-type(2) {
  z-index: 1;
}
.news .container .news__container:nth-of-type(3) {
  right: 5%;
  z-index: 0;
  margin-bottom: 90px;
}

.budget {
  position: relative;
  display: flex;
  justify-content: center;
  width: 100%;
  margin-bottom: 140px;
}
.budget .budget__gradient {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  z-index: -1;
  background-image: url("/assets/images/budget_images/budget_background.jpg");
  background-position: center;
  background-size: cover;
  filter: blur(5px);
  opacity: 0.2;
}
.budget .container {
  z-index: 1;
}
.budget .container .budget__container__left {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 45%;
  font-size: 22px;
  padding: 80px;
  color: black;
}
.budget .container .budget__container__left .budget__image {
  margin-top: 20px;
}
.budget .container .budget__container__left .budget__image img {
  width: 460px;
  transition: 0.3s;
}
.budget .container .budget__container__left .budget__image img:hover {
  transform: scale(1.2);
}
.budget .container .budget__container__right {
  width: 55%;
  padding: 80px;
}
.budget .container .budget__container__right .budget__form {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  background-color: #00000091;
  color: white;
  padding: 30px;
}
.budget .container .budget__container__right .budget__form p:nth-of-type(1) {
  font-size: 34px;
}
.budget .container .budget__container__right .budget__form p:nth-of-type(2) {
  font-size: 26px;
  margin-bottom: 30px;
  text-align: right;
}
.budget .container .budget__container__right .budget__form p:nth-of-type(3) {
  font-size: 26px;
  margin-bottom: 20px;
  text-align: right;
}
.budget .container .budget__container__right .budget__form #select_plan {
  padding: 10px 12px;
  font-size: 15px;
  background-color: var(--light-grey);
  outline: 3px solid var(--light-grey);
}
.budget .container .budget__container__right .budget__form #select_plan:hover {
  cursor: pointer;
}
.budget .container .budget__container__right .budget__form .budget__form__variations {
  display: flex;
  margin: 15px 0 30px 0;
}
.budget .container .budget__container__right .budget__form .budget__form__variations .budget__form__variation {
  background-color: transparent;
  padding: 10px 12px;
  margin-left: 10px;
  outline: 3px solid var(--light-grey);
  transition: 0.2s;
}
.budget .container .budget__container__right .budget__form .budget__form__variations .budget__form__variation:hover {
  cursor: pointer;
  background-color: var(--light-grey);
  color: black;
}
.budget .container .budget__container__right .budget__form .budget__form__variations .budget__form__variation--selected {
  background-color: var(--light-grey);
  color: black;
}
.budget .container .budget__container__right .budget__form .budget__form__infos {
  display: flex;
  flex-wrap: wrap;
}
.budget .container .budget__container__right .budget__form .budget__form__infos input {
  padding: 10px 12px;
  font-size: 15px;
  margin-bottom: 10px;
}
.budget .container .budget__container__right .budget__form .budget__form__infos input[type=text] {
  width: 60%;
}
.budget .container .budget__container__right .budget__form .budget__form__infos input[type=number] {
  width: calc(40% - 10px);
  margin-left: 10px;
}
.budget .container .budget__container__right .budget__form .budget__form__infos input[type=email] {
  width: calc(80% - 10px);
  margin-right: 10px;
}
.budget .container .budget__container__right .budget__form .budget__form__infos input[type=submit] {
  width: 20%;
  text-transform: uppercase;
  background-color: var(--grey-blue);
  color: white;
  letter-spacing: 1px;
  transition: 0.2s;
}
.budget .container .budget__container__right .budget__form .budget__form__infos input[type=submit]:hover {
  background-color: var(--light-grey);
  color: black;
}

.find-us {
  display: flex;
  justify-content: center;
  margin-bottom: 140px;
}
.find-us .container {
  height: 400px;
}
.find-us .container .find-us__container__left {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 50%;
  padding: 80px;
}
.find-us .container .find-us__container__left p {
  font-size: 20px;
  margin-bottom: 5px;
}
.find-us .container .find-us__container__left p:nth-of-type(1) {
  font-size: 34px;
  margin-bottom: 15px;
}
.find-us .container .find-us__container__right {
  width: 50%;
  padding: 60px 80px 60px 0;
}
.find-us .container .find-us__container__right .find-us__map {
  width: 100%;
  height: 100%;
}

.newsletter {
  display: flex;
  justify-content: center;
  margin-bottom: 160px;
}
.newsletter .container {
  padding: 0 80px;
  justify-content: space-between;
  align-items: center;
}
.newsletter .container .newsletter__text {
  display: flex;
  width: 30%;
  font-size: 20px;
}
.newsletter .container .newsletter__form {
  display: flex;
  align-items: center;
  width: calc(70% - 20px);
  margin-left: 20px;
  height: 40px;
}
.newsletter .container .newsletter__form input {
  padding: 10px 12px;
  font-size: 16px;
}
.newsletter .container .newsletter__form input[type=email] {
  width: calc(80% - 10px);
  margin-right: 10px;
  background-color: var(--light-grey);
}
.newsletter .container .newsletter__form input[type=submit] {
  width: 20%;
  background-color: var(--grey-blue);
  color: white;
  letter-spacing: 1px;
  transition: 0.2s;
}
.newsletter .container .newsletter__form input[type=submit]:hover {
  background-color: var(--light-grey);
  color: black;
}

.footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  background-color: var(--grey-blue);
}
.footer .footer__background {
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0.1;
}
.footer .footer__background img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.footer .container {
  flex-direction: column;
  z-index: 1;
}
.footer .container .footer__top {
  display: flex;
  width: 100%;
  justify-content: space-between;
}
.footer .container .footer__top .footer__infos, .footer .container .footer__top .footer__menus, .footer .container .footer__top .footer__social-medias {
  width: 33.3333333333%;
  padding: 50px 30px;
}
.footer .container .footer__top .footer__infos {
  display: flex;
  align-items: center;
}
.footer .container .footer__top .footer__infos a {
  display: flex;
  align-items: center;
  color: white;
}
.footer .container .footer__top .footer__infos a h1 {
  letter-spacing: 2px;
  font-weight: 400;
}
.footer .container .footer__top .footer__infos a h1:nth-of-type(1) {
  font-size: 36px;
}
.footer .container .footer__top .footer__infos a h1:nth-of-type(2) {
  font-size: 24px;
}
.footer .container .footer__top .footer__infos a .logo__divisor {
  background-color: white;
  height: 30px;
  width: 2px;
  margin: 0 12px;
}
.footer .container .footer__top .footer__menus ul, .footer .container .footer__top .footer__social-medias ul {
  height: 100%;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.footer .container .footer__top .footer__menus ul li, .footer .container .footer__top .footer__social-medias ul li {
  margin-bottom: 4px;
}
.footer .container .footer__top .footer__menus ul li a, .footer .container .footer__top .footer__social-medias ul li a {
  color: white;
  font-size: 18px;
  text-transform: uppercase;
}
.footer .container .footer__top .footer__menus {
  margin-right: 14%;
}
.footer .container .footer__top .footer__menus ul li a {
  transition: 0.2s;
}
.footer .container .footer__top .footer__menus ul li a:hover {
  letter-spacing: 2px;
}
.footer .container .footer__top .footer__social-medias {
  width: fit-content;
}
.footer .container .footer__top .footer__social-medias ul {
  align-items: flex-start;
}
.footer .container .footer__top .footer__social-medias ul li {
  margin-bottom: 6px;
}
.footer .container .footer__resposive {
  display: none;
  width: 100%;
  background-color: #3d495398;
  flex-direction: column;
}
.footer .container .footer__resposive a {
  color: white;
  width: 100%;
  text-align: center;
  font-size: 20px;
  padding: 10px 0;
}
.footer .container .footer__resposive a .icon {
  margin: 3px 5px 0 5px;
  padding-top: 3px;
  font-size: 20px;
}
.footer .container .footer__resposive .footer__resposive__menu {
  background-color: #3d495371;
  padding: 20px 0;
  transition: 0.2s;
}
.footer .container .footer__resposive .footer__resposive__menu ul {
  flex-direction: column;
  align-items: center;
}
.footer .container .footer__resposive .footer__resposive__menu ul li {
  margin: 2px 0;
}
.footer .footer__bottom {
  background-color: #3d495398;
  color: white;
  width: 100%;
  padding: 20px 0;
  font-size: 14px;
  text-align: center;
  letter-spacing: 2px;
  z-index: 1;
}